<template>
  <div class="ServiceAdminEdit">
    <h2>基本信息</h2>
    <el-divider></el-divider>
    <el-row :gutter="50">
      <el-col :span="8">
        <div class="name">上架时间：</div>
        <el-input v-model="form.hometownCity"></el-input>
      </el-col>
      <el-col :span="8">
        <div class="name">下架时间：</div>
        <el-input v-model="form.schoolName"></el-input>
      </el-col>
    </el-row>
    <!-- 图片上传 -->
    <div class="imgupload" style="padding-top:20px">
      <div class="name">上传图片：</div>
      <div>
        <div>
          <div style="padding-bottom:20px">
            <img
              src="https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1594011875817&di=7268ba85ce825bad5e6fc134b777a413&imgtype=0&src=http%3A%2F%2Fimg2.imgtn.bdimg.com%2Fit%2Fu%3D3930510857%2C1870350260%26fm%3D214%26gp%3D0.jpg"
              width="100%"
            />
          </div>
          <div>
            <el-upload
              class="upload-demo"
              ref="upload"
              action="https://jsonplaceholder.typicode.com/posts/"
              :on-preview="handlePreview"
              :on-remove="handleRemove"
              :file-list="fileList"
              :auto-upload="false"
            >
              <el-button
                slot="trigger"
                size="small"
                type="primary"
              >{{fileList.length > 0 ? '重新上传' : '选取文件'}}</el-button>
              <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div>
            </el-upload>
          </div>
        </div>
      </div>
    </div>
    <el-row :gutter="50">
      <el-col :span="8">
        <el-button @click="close">关闭</el-button>
        <el-button @click="preservation">保存</el-button>
      </el-col>
    </el-row>
  </div>
</template>
<script>
export default {
  data() {
    return {
      //处理类型 ， 用户判断是举报还是反馈
      handleType: this.$route.query.type,
      fileList: [
        {
          name: "food.jpeg",
          url:
            "https://fuss10.elemecdn.com/3/63/4e7f3a15429bfda99bce42a18cdd1jpeg.jpeg?imageMogr2/thumbnail/360x360/format/webp/quality/100"
        }
      ],
      //表单数据
      form: {
        name: "王大拿", //昵称
        sex: 1, //性别
        hometownCity: "", //家乡城市
        schoolName: "", //学校
        birthday: "", //出生日期
        autograph: "" //个性签名
      }
    };
  },
  methods: {
    //删除图片
    handleRemove(file, fileList) {
      console.log(file, fileList);
      this.fileList.shift();
    },
    //添加图片
    handlePreview(file) {
      console.log(file);
    },
    // 保存
    preservation() {
      this.$confirm("是否保存？", {
        confirmButtonText: "保存",
        cancelButtonText: "取消"
      })
        .then(() => {
          this.$message({
            message: "保存成功",
            type: "success"
          });
          this.$router.go(-1);
        })
        .catch(() => {});
    },
    //关闭
    close() {
      this.$confirm("是否关闭？", {
        confirmButtonText: "关闭",
        cancelButtonText: "取消"
      }).then(() => {
        this.$router.go(-1);
      });
    }
  }
};
</script>
<style lang="scss" scoped>
.ServiceAdminEdit {
  padding-bottom: 200px;
}
h2 {
  padding-top: 30px;
}
.imgupload {
  display: flex;
  > div:nth-child(2n) {
    width: 50%;
    display: flex;
    .el-upload__tip {
      padding: 15px 0;
    }
  }
}
.bigGift {
  padding: 30px 0;
  display: flex;
}
</style>